import React, {useState, useEffect} from 'react';
import {AccountDetails} from "./AccountDetails";
import './App.css';
import { useNavigate } from 'react-router-dom';
import {toast} from 'react-toastify';

export const Login=()=> {
    
    const initialValues = {userName:"", password :""};
    const [formValues, setFormValues] = useState(initialValues);
    const [users, setUsers] = useState([]);
    const[accounts, setAccounts] = useState([]);
    const navigate=useNavigate();

    const handleChange=(event)=>{
        //console.log(event.target);
        const{name, value} = event.target;
        setFormValues({...formValues, [name]: value});
    }
    
    const handleSubmit=(event)=>{
            event.preventDefault();
            for(let i=0;i<users.length;i++){
                if(users[i].userName === formValues.userName && users[i].password === formValues.password)  {
                    let j=users[i].id;
                    //get checking account from accounts where id=j
                    navigate('/accountdetails',{state: {
                        checkingAccount: accounts[j-1].checkingAccount,
                        savingsAccount: accounts[j-1].savingsAccount,
                        creditcardAccount: accounts[j-1].creditcardAccount,
                    }});
                    break;
                }else{
                    navigate('/login');
                }  
            }
        }

    const fetchDataforUsers = () => {
          fetch("http://localhost:8000/users/")
            .then(response => {
              return response.json();
            })
            .then(json => {
                json.map(data=>{
                    setUsers(users => [...users,data]);
                })     
            })
        }

        const fetchDataforAccounts = () => {
            fetch("http://localhost:8001/accounts/")
              .then(response => {
                return response.json();
              })
              .then(json => {
                  json.map(data=>{
                    setAccounts(accounts => [...accounts,data]);
                      console.log(data);
                  })     
              })
          }
          
        useEffect(() => {
            fetchDataforUsers();
            fetchDataforAccounts();
        }, [])
    
    return (
        <div className="ui fixed menu">
            <h2>Login</h2>
            <div className="ui container center">
                <form class="needs-validation" onSubmit={handleSubmit}>
                    <div class="form-group was-validated">
                        <label class="form-label" htmlFor="userName">UserName</label>
                        <input class="form-control" type="text" name="userName" onChange={handleChange} value={formValues.userName}/>
                        <div class="invalid-feedback">Please enter your username</div>
                    </div>
                    <div class="form-group was-validated">
                        <label class="form-label" htmlFor="password">Password</label>
                        <input class="form-control" type="password" name="password" onChange={handleChange} value={formValues.password}/>
                        <div class="invalid-feedback">Please enter your password</div>
                    </div>
                    <button class="btn btn-success w-100" type="submit" value="Login">Login</button>
                </form>
            </div>
        </div>
    );
}

